import "./Blog.scss";
import Img from "./../../images/d.jpg";
import Img2 from "./../../images/bg.jpg";
import Img3 from "./../../images/books-beside-jar-and-eyeglsses-3698600.jpg";
import Profile1 from "./../../images/1114_7271_230710001584166822.jpeg";
import Profile2 from "./../../images/3150_5202_187195001584166697.jpeg";
import Profile3 from "./../../images/3555_8785_379686001584167341.jpeg";


const Blog = () => {
	const BlogDetails = [
		{ id: "1", proImg: Img, profileImg: Profile1, name: "Jon Deo", designation: "Developer", title: "What is API", desc: "Lorem ipsum dolor sit amecessitatibus consequatur sequi, ab placeat ipsam magni iste autem et iusto fuga." },
		{ id: "2", proImg: Img3, profileImg: Profile2, name: "Peter", designation: "CTO wx", title: "What is CMCO", desc: "Lorem ipsum dolor sit amecessitatibus consequatur sequi, ab  Lorem ipsum dolor sit amecessitatibus consequatu placeat ipsam magni iste autem et iusto fuga." },
		{ id: "3", proImg: Img2, profileImg: Profile3, name: "Lissa Marker", designation: "Designer", title: "How to work with Masonary Layout", desc: "Lorem ipsum dolor sit amecessitatibus consequatur sequi, ab placeat ipsam magni iste autem et iusto fuga." },
		{ id: "4", proImg: Img, profileImg: Profile2, name: "Waren Finch", designation: "Product Head", title: "What is API", desc: "Lorem ipsum dolor sit amecessitatibus consequatur sequi, ab placeat ipsam magni iste autem et iusto fuga." },
	]
	return (
		<div className="blog">
			<div className="container">
				<div className="row">
					<div className="col-12">
						<h2>Our Blog Stories</h2>
						<p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Eumit nulla esse.</p>
					</div>
					<div className="col-lg-8 col-12">
						{BlogDetails.map((data, index) => (
							<div className="blogWide" data-aos="fade-down" data-aos-duration="1000" key={index}>
								<figure className="blogImg">
									<img src={data.proImg} alt="no-img" />
								</figure>
								<div className="blogBlock">
									<div className="blogDetails">
										{data.title && <h4>{data.title}</h4>}
										{data.desc && <p>{data.desc}</p>}
									</div>
									<div className="blogProfile">
										<div>
											<figure className="profileImg">
												<img src={data.profileImg} alt="no-img" />
											</figure>
											<div>
												{data.name && <h5>{data.name}</h5>}
												{data.designation && <p> {data.designation}</p>}
											</div>
										</div>
										<div className="blogTime">
											2min ago
										</div>
									</div>
								</div>
							</div>))}

					</div>
					<div className="col-lg-4 col-12">
						{BlogDetails.map((data, index) => (
							<div className="blogSmall" data-aos="fade-down" data-aos-duration="1000" key={index}>
								<figure>
									<img src={data.proImg} alt="no-img" />
								</figure>
								<div className="blogDetails">
									<h4>{data.title}</h4>
									<p>{data.desc}</p>
									<div className="publishTime">2min ago</div>
								</div>
							</div>))}
					</div>
				</div>
			</div>
		</div >
	)
}

export default Blog;