import "./TextBox.scss";

const TextBox = ({ helperText, error, className, type, placeholder, tabIndex, id }) => {
	return (
		<div className={`${className} textBox`}>
			<input type={type} placeholder={placeholder} onChange={onchange} id={id} tabIndex={tabIndex} />
			{helperText ? <p className="helpText">{helperText}</p> : false}

			{error ? <p className="error">{error}</p> : false}

		</div>
	)
}
export default TextBox;