import "./FeedBack.scss";
import Slider from "react-slick";
import BackQuote from "./../../Svg/BackQuote";
import Profile1 from "./../../images/1114_7271_230710001584166822.jpeg";
import Profile2 from "./../../images/3150_5202_187195001584166697.jpeg";
import Profile3 from "./../../images/3555_8785_379686001584167341.jpeg";
import LinkedinIcon from "../../Svg/LinkedInIcon";
import InstaIcon from "../../Svg/InstaIcon";
import FacebookIcon from "../../Svg/FacebookIcon";
import GoogleIcon from "../../Svg/GoogleIcon";


const FeedBack = () => {
	const settings = {
		infinite: false,
		speed: 500,
		slidesToShow: 3,
		slidesToScroll: 3,
		dots: true,
		responsive: [
			{
				breakpoint: 640,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
				}
			},
			{
				breakpoint: 940,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1,
				}
			},
		]

	}

	const feedBack = [
		{ id: "1", name: "Jon Deo", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Deserunt dti se obcaecati? ex off? Nostrum, ex officiis", designation: "Client Manger", img: Profile1 },
		{ id: "2", name: "Waren Pintch", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Deserunt dti se obcaecati? ex off? Nostrum, ex officiis", designation: "Product Head", img: Profile2 },
		{ id: "3", name: "Lissa Marker", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Deserunt dti se obcaecati? ex off? Nostrum, ex officiis", designation: "Designer", img: Profile3 },
		{ id: "4", name: "Peter", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Deserunt dti se obcaecati? ex off? Nostrum, ex officiis", designation: "CTO Wx", img: Profile1 },
	];

	return (
		<div className="feedBack">
			<div className="container ">
				<h2>What they Say?</h2>
				<p>Lorem ipsum dolor sit amet consecteturt ipsam temporibus, reprehenderit ad exercitationem voluptatum excepturi maxime.</p>
				<div className="row">
					<div className="col-12">
						<Slider {...settings}>
							{feedBack.map((desc, index) => (
								<div key={index} data-aos="zoom-in-up">
									<div className="feedbackWrapper">
										<div className="feedBackBlock">
											<div className="quote top"><BackQuote /></div>
											<p className="feedbackDetails">{desc.desc}</p>
											<div className="quote bottom"><BackQuote /></div>
										</div>
										<div className="feedbackProfile">
											<figure>
												<img src={desc.img} alt="no-found" />
											</figure>
											<div className="profileDesc">
												<h5>{desc.name}</h5>
												<p>{desc.designation}</p>
											</div>
										</div>
										<div className="socialIcon">
											<ul>
												<li><LinkedinIcon height="1.3rem" width="1.3rem" /></li>
												<li><InstaIcon height="1.3rem" width="1.3rem" /></li>
												<li><FacebookIcon height="1.3rem" width="1.3rem" /></li>
												<li><GoogleIcon height="1.3rem" width="1.3rem" /></li>

											</ul>
										</div>
									</div>
								</div>))}

						</Slider>
					</div>
				</div>
			</div >
		</div>
	)
}

export default FeedBack;