import "./Hero.scss";
import Img from "./../../images/businessman-touching-virtual-screen.jpg";
import Button from "../Button/Button";

const Hero = () => {
	return (
		<div className="hero">
			<div className="container-fluid">
				<div className="row no-gutters">
					<div className=" col-12">
						<div className="heroImgWrap">
							<img src={Img} alt="no-img" />
						</div>
					</div>
					<div className="heroDesc">
						<div className="heroDetails">
							<div className="heroWrap">
								<h2 className="mainHeading">WE ARE DIGITAL AGENCY</h2>
								<p>UI99 is providing lost of things for your business like poster,presentation,web-design,templates. Lorem ipsum dolor sit amet consectetur adipisicing elit.</p>
								<Button className="primaryBtn" label="Read More" />
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default Hero;