import "./Project.scss";
import Img1 from "./../../images/d.jpg";
import Img2 from "./../../images/bg.jpg";
import Img3 from "./../../images/books-beside-jar-and-eyeglsses-3698600.jpg";

import Button from "../Button/Button";

const Project = () => {
	const projects = [
		{ id: "1", img: Img1, title: "Web Analysis", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "2", img: Img2, title: "Web Design", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "3", img: Img3, title: "Web Analysis", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },

	]
	return (
		<div className="project">
			<div className="container">
				<div className="row">
					<div className="col-12">
						<h2>Our Projects</h2>
						<p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dicta nisi,a!</p>
					</div>
					{projects.map((data, index) => (
						<div className="col-lg-4 col-md-6 col-12" data-aos="fade-down" data-aos-duration="2000" key={index}>
							<div className="projectWrapper">
								<div className="projectImg">
									<img src={data.img} alt="no-img" />
								</div>
								<div className="projectTitle">
									{data.title && <h3>{data.title}</h3>}
									{data.desc && <p>{data.desc}</p>}
									<Button className="linkBtn" label="Read More" />
								</div>
							</div>
						</div>))}
				</div>
			</div>
		</div>
	)
}

export default Project;