import { useState } from "react";
import MenuIcon from "./../../Svg/MenuIcon";
import "./Header.scss";

const Header = ({ tab }) => {
	const [isOpen, setOpen] = useState(false);
	return (
		<div className={`headerWrapper ${tab ? "stickyHeader" : false}`}>
			<div className="logo">
				UI99
			</div>
			<div className="menu">
				<ul className="deskMenu">
					<li>Home</li>
					<li>Partners</li>
					<li>About Us</li>
					<li>Services</li>
					<li>Projects</li>

					<li>Feedback</li>
					<li>Blog</li>
					<li>Pricing</li>
					<li>ContactUs</li>
					<li>Login</li>
				</ul>
				<div className="mobMenu">
					<button className="iconLinkBtn" onClick={() => setOpen(!isOpen)}><MenuIcon height="2rem" width="2rem" /></button>
				</div>
				{isOpen ?
					<ul className="mobMenuList">
						<li>Home</li>
						<li>Partners</li>
						<li>About Us</li>
						<li>Services</li>
						<li>Feedback</li>
						<li>Blog</li>
						<li>Pricing</li>
						<li>ContactUs</li>
						<li>SignUp</li>
						<li>Login</li>
					</ul> : false
				}
			</div>
		</div>
	)
}

export default Header;