import "./Team.scss";
import Profile3 from "../../images/3150_5202_187195001584166697.jpeg";
import Profile2 from "./../../images/1114_7271_230710001584166822.jpeg";
import Profile1 from "./../../images/3555_8785_379686001584167341.jpeg";
import Profile4 from "./../../images/2979_5573_887614001584167834.jpeg";


const Team = () => {
	const team = [
		{ id: "1", img: Profile1, name: "Jon Deo", designation: "Account Head" },
		{ id: "2", img: Profile2, name: "Lissa Roy", designation: "Product Head" },
		{ id: "3", img: Profile3, name: "Rachel Stephan", designation: "CMS" },
		{ id: "4", img: Profile4, name: "Sagar More", designation: "CEO at Wix" },


	]
	return (
		<div className="team">
			<div className="container">
				<div className="row">
					<div className="col-lg-7 col-sm-6 col-12 teamDesc">
						<h2>Speakers</h2>
						<p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Illo, magnam offiluptate? Lorem, ipsum dolor sit amet consectetur adipisicing elit. tempora harum, provident ea distinctio sint id itaque dicta reprehenderit.</p>
					</div>

					<div className="col-lg-5 col-sm-6 col-12" data-aos="fade-left" data-aos-duration="3000" data-aos-delay="200">
						<div className="speakerWrapper speakerWrapperMain">
							<figure className="speakerImg">
								<img src={Profile3} alt="no-found" />
							</figure>
							<div className="speakerProfile">
								<h5>Jon Deo</h5>
								<p>Designer</p>
							</div>
						</div>
					</div>
					{team.map((data, index) => (
						<div className="col-lg-3 col-md-4 col-sm-6 col-12" key={index}>
							<div className="speakerWrapper">
								<figure className="speakerImg">
									<img src={data.img} alt="no-found" />
								</figure>
								<div className="speakerProfile">
									<h5>{data.name}</h5>
									<p>{data.designation}</p>
								</div>
							</div>
						</div>))}
				</div>
			</div>
		</div>
	)
}
export default Team;